.register-product {
    .main-upload-img {
        background: #e6e6e6;
        min-height: 360px;
        position: relative;

        .upload-img-product {
            display: flex;
            align-items: center;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            h2 {
                padding-left: 10px;
                margin: 0;
                opacity: 0.5;
                font-family: NanumSquare;
                font-size: 25px;
                font-weight: bold;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.4;
                letter-spacing: normal;
                text-align: left;
                color: #111111;
            }
        }
        .img-product-detail {
            position: relative;
            .img-detail {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            .remove-img {
                width: 26px;
                height: 26px;
                padding: 7px;
                background-color: #000000;
                position: absolute;
                right: 0;
                top: 0;
                img {
                    display: block;
                    margin: auto;
                }
            }
        }
    }
    .main-product-form {
        padding: 20px;
        &__item {
            padding-bottom: 19px;
            &.check-on-sale {
                padding-bottom: 14px;
                .label-radio {
                    width: calc(100%/2 - 20px);
                    span {
                        display: flex;
                        align-items: center;
                    }
                }
            }
            &__title {
                font-family: NanumSquare;
                font-size: 14px;
                font-weight: 800;
                font-stretch: normal;
                font-style: normal;
                line-height: normal;
                letter-spacing: normal;
                text-align: left;
                color: #111111;
                padding-bottom: 10px;
                strong {
                    color: #c52321;
                    padding-left: 3px;
                }
            }
            .main-check {
                font-weight: normal;
                font-stretch: normal;
                font-style: normal;
                line-height: normal;
                letter-spacing: normal;
            }
        }
    }
}